:root {
  --background-color: #596275;
  --square-color: #303952;
  --highlight-color: #3dc1d3;
  --button-color: #574b90;
}

.app {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--background-color);
}

.title {
  margin-top: 0vh;
  color: var(--highlight-color);
  font-size: 3rem;
}

button {
  outline: none;
  border: none;
}

.start-screen {
  position: absolute;
}

.overlay {
  width: 100vw;
  height: 100vh;
}

.screen__controls {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;

  width: 100vw;

  background: rgb(48, 57, 82);
  background: linear-gradient(
    0deg,
    rgba(48, 57, 82, 0) 0%,
    rgba(48, 57, 82, 0.4) 5%,
    rgba(48, 57, 82, 0.6) 10%,
    rgba(48, 57, 82, 0.8) 15%,
    rgba(48, 57, 82, 1) 25%,
    rgba(48, 57, 82, 1) 75%,
    rgba(48, 57, 82, 0.8) 85%,
    rgba(48, 57, 82, 0.6) 90%,
    rgba(48, 57, 82, 0.4) 95%,
    rgba(48, 57, 82, 0) 100%
  );

  padding: 8rem 0;

  display: flex;
  flex-direction: column;

  justify-content: center;
  align-items: center;

  gap: 0.5rem;
}

.btn {
  width: 9rem;
  aspect-ratio: 3/1.2;
  border-radius: 10px;
}

.btn-start {
  background-color: var(--background-color);
  font-weight: bold;
  font-size: 1.5rem;
  color: var(--highlight-color);
}

.btn-start:hover {
  color: var(--background-color);
  background-color: var(--highlight-color);
}

.board {
  height: 75%;
  aspect-ratio: 1/1;
  /* background-color: blue; */
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

.square {
  width: 32%;
  height: 33%;
  border-radius: 15px;
  background-color: var(--square-color);
}

.cross {
  color: #63cdda;
}

.circle {
  color: #e15f41;
}
